import React from 'react';
import Image from 'next/image';
import {assets} from "@/assets/assets";


const Header = () => {
  return (
    <div className='flex flex-col items-center justify-center h-screen text-center gap-4 px-5 lg:px-8 
    xl:px-[8%] bg-gradient-to-b from-[#f9f9f9] to-[#fff] relative'
    >
        <div>
            <Image src={assets.mypic} width={400} height={400} alt='' className='rounded-full w-32'
            style={{ marginLeft: '24rem', marginRight: '27rem', marginBottom: '-2rem', marginTop:'9rem' }}/>
        </div>
        <h3 className='text-[30px] sm:text-[80px] lg:text-[100px] font-Ovo'>Hi I am Musa Dick Banda
            <Image src={assets.hand_icon} alt='' width={80}/></h3>
        <h1 className='text-[60px] sm:text-[80px] lg:text-[100px] font-Ovo'> Front end web developer based South Africa</h1>
        <p className='text-[30px] sm:text-[80px] lg:text-[100px] font-Ovo'>
        I am new front web developer with 0 expirience, so well I gues I'm still learning</p>
        <div className='flex flex-col sm-flex-row items-center gap-4 mt-4'>
            
            <button><a href="#contact" className=''><Image src={assets.my} alt='' width={260}/></a></button>

            <button><a href="/sample-resume.pdf" download className=''><Image src={assets.my2} alt='' width={260} />
            </a></button>
        </div>
    </div>
  );
}

export default Header;